.body-mentor {
    font-family: "FF Real Head";
    text-align: center;
}
.container {
    padding: 0 5px;
}
.logo {
    width: 100px;
}
.heading-mentor {
    font-size: 45px;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-weight: 300;
}
.paragraph-mentor {
    font-size: 17px;
    text-transform: uppercase;
    width: 80%;
    margin: 0 auto;
    margin-bottom: 30px;
}
.typeform__widget_mentor {
    border: 3px solid #fcd0c5;
    border-bottom: none;
    height: 250px !important;
}
.screen__StickyFooter-sc-9mfxmi-10.eHwAog::after{
    background-color: transparent !important;
    box-shadow: transparent 0px 0px !important;
    }
/* HEADER */

.header-mentor {
    div.container{
        div{
            display: flex;
            justify-content: space-around;
            align-items: center;
            a {
                text-transform: uppercase;
                font-size: 14px;
                letter-spacing: 0.7px;
                cursor: pointer;
                height: 33px;
                width: 135px;
                max-width: 100%;
                background-color: #fceeee;
                color: #f2b3ae;
                box-shadow: 3px 3px 0px 0px  #f2b3ae;
                transition: 0.25s ease-out;
                padding-top: 6px;
            }
            a:nth-of-type(1){
                margin-right: 8px;
            }
            a:nth-of-type(2){
                margin-left: 8px;
            }
            a:hover {
                box-shadow: 1px 1px 0px 0px #F2B3AE !important;
                transform: translateX(2px) translateY(2px);
            }
        }
    }
}

/* MENTOR SECTION */

.section-mentor {
    background: url("../../themes/images/Mask_BG.png");
    color: white;
    padding-top: 60px;
    padding-bottom:40px;
    h1 {
        margin-bottom: -80px;
    }
    span {
        font-size: 150px;
        font-family: "SebastianBobbyAlt";
        text-transform: capitalize;
        margin-bottom: 30px;
    }
}

/* FEEDBACK SECTION */

.section-feedback {
    color: #f2b3ae;
    padding-top: 20px;
}

/* BONUS SECTION */

.section-bonus {
    color: white;
    background: url("../../themes/images/Mist_BG.png");
    padding-top: 60px;
    padding-bottom: 100px;
    div.container {
        margin-top: -20px;
        margin-bottom: 48px;
    }
}

/* TYPEFORM-WHITE SECTION */

.typeform-white {
    .form_cont {
        margin-top: -100px;
        margin-bottom: 50px;
    }
    .typeform__widget_mentor {
        border-bottom: 3px solid #fcd0c5;
    }
}

/* FOOTER */

.footer-mentor {
    background: url("../../themes/images/Mask_BG.png");
    padding: 15px 0;
    color: white;
    p {
        margin-top: -8px;
    }
}


@media screen and (max-width: 768px) {
    .heading-mentor {
        font-size: 40px;
        line-height: 40px;
    }
    .header-mentor {
        div.container{
            div{
                a {
                    height: 40px;
                    padding-top: 9.5px;
                }
            }
        }
        .logo {
            width: 80px;
        }
    }
    .section-feedback {
        .heading-mentor {
            line-height: normal;
        }
    }
}