* {
    margin:0;
    padding:0;
}

@import url(//db.onlinewebfonts.com/c/d645cc4a58c3bcf8ab61869b4edab482?family=FF+Real+Head);
@font-face {font-family: "FF Real Head"; src: url("//db.onlinewebfonts.com/t/d645cc4a58c3bcf8ab61869b4edab482.eot"); src: url("//db.onlinewebfonts.com/t/d645cc4a58c3bcf8ab61869b4edab482.eot?#iefix") format("embedded-opentype"), url("//db.onlinewebfonts.com/t/d645cc4a58c3bcf8ab61869b4edab482.woff2") format("woff2"), url("//db.onlinewebfonts.com/t/d645cc4a58c3bcf8ab61869b4edab482.woff") format("woff"), url("//db.onlinewebfonts.com/t/d645cc4a58c3bcf8ab61869b4edab482.ttf") format("truetype"), url("//db.onlinewebfonts.com/t/d645cc4a58c3bcf8ab61869b4edab482.svg#FF Real Head") format("svg"); }


section.apply_section{
    div.container{
        background-image: url("../../themes/images/Mask_BG.png");
        max-height: 500px;
        display: flex;
        justify-content: center;
        div.apply_cont2{
            display: flex;
            flex-direction: column;
            justify-content: center;
            margin-bottom: 90px;
            padding: 85px;
            @media (max-width: 600px){
                padding: 20px;
            }
            @media (max-width: 500px){
                padding: 10px 0;
            }
            h1{
                margin: 0 auto 30px auto;
                font-size: 70px;
                letter-spacing: 2px;
                font-weight: 400;
                font-family: "FF Real Head";
                text-transform: uppercase;
                color: #fff;
                @media (max-width: 1200px){
                    font-size: 55px;
                }
                @media (max-width: 1000px){
                    font-size: 45px;
                }
                @media (max-width: 900px){
                    text-align: center;
                    font-size: 60px;
                }
                @media (max-width: 450px){
                    font-size: 50px;
                }
                .second_head{
                    @media (max-width: 900px){
                        display: block;
                        margin-top: -26px;
                    }
                    @media (max-width: 450px){
                        margin-top: -25px;
                    }
                }
                .sebastian{
                    font-family: "SebastianBobbyAlt";
                    text-transform: capitalize;
                    position: relative;
                    font-size: 205px;
                    letter-spacing: 0;
                    top: 30px;
                    right: 30px;
                    letter-spacing: 1px;
                    @media (max-width: 1200px){
                        font-size: 160px;
                    }
                    @media (max-width: 1000px){
                        font-size: 150px;
                    }
                    @media (max-width: 900px){
                        display: block;
                        margin-top: -120px;
                        text-align: center;
                        right: 0;
                    } 
                    @media (max-width: 450px){
                        margin-top: -100px;
                        font-size: 120px;
                    }
                }
            }
            button{
                a{
                    width: 150px;
                    font-size: 30px;
                    padding: 10px 110px;
                    color: #F2B3AE !important;
                    border-color: #F2B3AE !important;
                    background-color: #FFFFFF;
                    box-shadow: 3px 3px 0px 0px #F2B3AE !important;
                    transition: transform .25s ease-out, opacity .25s ease-out, box-shadow .25s ease-out, opacity .25s ease-out, background 0s ease-out .25s;
                    text-transform: uppercase;
                    font-family: "FF Real Head";
                    @media (max-width: 550px){
                        padding: 10px 80px;
                    }
                    @media (max-width: 450px){
                        padding: 10px 30px; 
                    }
                    &:hover{
                        box-shadow: 1px 1px 0px 0px #F2B3AE !important;
                        outline: 0;
                        transform: translateX(2px) translateY(2px);
                    }
                }
            }
        }
    }
}

section.desc_section{
    div.container{
        background-color: #ffffff;
        display: flex;
        flex-direction: column;
        .apply_cont{
            button{
                a{
                    @media (max-width: 748px){
                        font-size: 24px;
                        padding: 10px 50px;
                    }
                    @media (max-width: 500px){
                        font-size: 16px;
                        padding: 15px 50px;
                    }
                }
            }
        }
        h4{
            padding: 60px 125px;
            font-size: 58px;
            color: #f3b9b4;
            text-align: center;
            text-transform: uppercase;
            @media (max-width: 1200px){
                font-size: 48px;
            }
            @media (max-width: 1000px){
                font-size: 42px;
            }
            @media (max-width: 749px){
                font-size: 38px;
                padding: 60px 50px;
            }
            @media (max-width: 500px){
                font-size: 28px;
                padding: 40px 25px;
            }
        }
        p{
            color: black;
            font-size: 28px;
            font-family: "FF Real Head";
            padding: 0 100px;
            text-align: center;
            @media (max-width: 748px){
                font-size: 22px;
                padding: 0 70px;
            }
            @media (max-width: 600px){
                font-size: 18px;
                padding: 0 35px;
            }
        }
        div.icon_cont{
            div.icon{
                display: grid;
                grid-template-columns: 400px 400px;
                grid-template-rows: 130px 130px 130px 130px;
                gap: 1px 100px;
                justify-content: center;
                @media (max-width: 900px){
                    grid-template-columns: 300px 300px; 
                    grid-template-rows: 130px 130px 130px 130px;
                }
                @media (max-width: 700px){
                    grid-template-columns: 80%; 
                    grid-template-rows: 1fr;
                    gap: 20px;
                }
                @media (min-width: 1900px){
                    grid-template-columns: 600px 600px;
                    grid-template-rows: 260px 260px 260px 260px;
                }
                div.contain{
                    display: flex;
                    justify-content: center;
                    div{
                        max-width: 80px;
                        @media (max-width: 1000px){
                            max-width: 70px;
                        }
                        @media(max-width: 700px){
                            justify-content: start;
                        }
                        @media (min-width: 1900px){
                            max-width: 120px;
                        }
                    }
                    p{
                        font-size: 18px;
                        padding: 0 45px;
                        color: white;
                        @media (max-width: 1000px){
                            font-size: 16px;
                        }
                        @media (max-width: 500px){
                            font-size: 15px;
                            padding: 0 20px;
                        }
                        @media (min-width: 1900px){
                            font-size: 24px;
                        }
                    }
                }
            }
        }
    }
}

section.amb_first{
    margin-top: -10px;
    div.container{
        background-image: url("../../themes/images/micellar_BG.png");
        display: flex;
        @media (min-width: 1900px){
            padding-bottom: 30px;
        }
        div.amb_port{
            width: 500px;
            position: relative;
            bottom: 40px;
            left: -80px;
            z-index: 1;
            @media (max-width: 1000px){
                width: 400px;
            }
            @media (max-width: 748px){
                width: 350px;
            }
            @media (max-width: 600px){
                width: 300px;
                bottom: 10px;
                left: -50px;
            }
            @media (max-width: 500px){
                width: 230px;
                bottom: 10px;
                left: -40px;
            }
            @media (max-width: 450px){
                width: 180px;
            }
            img{
                border-radius: 50%;
            }
        }
        div.apply_cont{
            margin: 70px auto 0px auto;
            padding: 0 55px;
            @media (max-width: 600px){
                margin: 70px auto 70px auto;
                padding: 0; 
                padding-right: 10px;
            }
            @media (max-width: 450px){
                margin: 40px auto 40px auto;
            }
            @media (min-width: 1920px){
                margin: 100px auto 0 auto;
            }
            h1{
                margin: 0 auto 45px auto;
                font-size: 45px;
                @media (max-width: 748px){
                    font-size: 25px;
                    margin: 0 auto 25px auto;
                }
                span{
                    position: absolute;
                    font-size: 140px;
                    bottom: -23px;
                    letter-spacing: 0;
                    right: 150px;   
                    @media (max-width: 748px){
                        font-size: 70px;
                        bottom: -13px;
                        right: 75px;
                    }             
                }
            }
            p{
                font-size: 2vw;
                font-family: "FF Real Head";
                color: black;
                text-align: center;
                line-height: 2;
                span{
                    background: white;
                    height: 2px;
                    width: 100%;
                    display: block;
                }
            }
        }
    }
}

section.video{
    margin-top: -10px;
    div.container{
        background-color: #000;
        margin: 0 15% 100px 15%;
        height: 50vw;
        max-height: 500px;
        z-index: 1;
        @media (max-width: 748px){
            margin: 0;
        }
        div.button{
            position: absolute;
            text-align: center;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            transition: color 4s linear;
            @media (max-width: 1000px){
                width: 100px;
            }
            @media (max-width: 600px){
                width: 70px;
            }
            @media (max-width: 450px){
                width: 50px;
            }
            button{
                background: white;
                border-radius: 50%;
                cursor: pointer;
            }
            &:hover{
                button{
                    background-color: #fceeee;
                }
            }
        }
    }
}

